<template>
  <div v-show="visible" class="paso">
    <slot></slot>
    <button @click="validar" id="validar">Validar</button>
    <button @click="enviar" id="enviar" :disabled="isDisabled">Enviar</button>
  </div>
</template>

<script>
export default {
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
    isDisabled: {
      type: Boolean,
      default: true,
    },
  },
  methods: {
    validar() {
      this.$emit("validar");
    },
    enviar() {
      this.$emit("enviar");
    },
  },
};
</script>

<style scoped>
/* Estilos del paso */
</style>
